.swiper-container {
    @media (min-width: $desktop) {
        width: 820px;
    }
}

.swiper-button-prev {
    @media (max-width: $desktop) {
        display: none;
    }
 
    &::after {
        display: none;
    }
}

.swiper-button-next {
    @media (max-width: $desktop) {
        display: none;
    }

    &::after {
        display: none;
    }

}

