.menu-list {
    
    @media (min-width: $desktop) {
        display: block;
    }

    @media (min-width: $desktop) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        background-color: transparent;
        width: 620px;
    }
}

.menu-list__item {
    background-color: $main-dark;
    border-top: 1px solid #636E79;
    border-bottom: 1px solid $dark;

    @media (min-width: $desktop) {
        background-color: transparent;
        border: none;
    }

}

.menu-list__link {
    font-size: 18px;
    font-weight: 700;
    line-height: 32px;
    text-transform: uppercase;
    text-align: center;
    color: $light;  

    padding-top: 14px;
    padding-bottom: 16px;

    &:hover,
    &:focus {
        color: $accent;
    }

    &:active {
        opacity: 0.3;
    }

    @media (min-width: $desktop) {
        font-size: 18px;
        line-height: 18px;
        font-weight: 400;
        text-transform: capitalize;
        padding: 0;
        margin: 0;
    }

    
}
