.reviews {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 41px;

    @media (min-width: $desktop) {
        position: relative;

        width: 940px;
        margin: 0 auto;
    }
}



.reviews__slider .swiper-pagination-bullets {
    @media (min-width: $desktop) {
        display: none;
    }
}

.slider__list {
}

.slider__item {
    
    
}

.slider__button {

    &:hover,
    &:focus {
        .slider__button-icon {
            fill: $main-dark;
            opacity: 0.6;
        }
    }

    &:active {
        opacity: 0.1;
    }
}





