.contacts {
    padding-top: 50px;
    padding-bottom: 22px;
}

.contacts__wrapper {
    text-align: center;
}

.contacts__title {
    font-weight: 300;
    font-size: 24px;
    line-height: 120%;
    margin-bottom: 45px;

    @media (min-width: $tablet) {
        margin-left: 79px;
        margin-right: 79px;
        font-size: 40px;
        line-height: 54px;
    }

    @media (min-width: $desktop) {
        font-size: 50px;
        line-height: 68px;
    }
}

.contacts__inner {
    padding-left: 40px;
    padding-right: 40px;

    @media (min-width: $tablet) {
        padding-left: 67px;
        padding-right: 67px;
    }

    @media (min-width: $desktop) {
        width: 940px;
        margin: 0 auto;
        padding-right: 47px;
        padding-right: 47px;
    }
}

.contacts__list {

    @media (min-width: $tablet) {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    @media (min-width: $desktop) {
        justify-content: space-between;
    }
}

.contacts__item {
    margin-bottom: 18px;

    @media (min-width: $tablet) {
        margin-right: 97px;
        margin-bottom: 55px;
    }

    @media (min-width: $desktop) {
        margin-right: 0;
    }

    &:nth-child(2) {

        @media (min-width: $tablet) {
            margin-right: 0;
        }
        
    }

    &:nth-child(3) {

        @media (min-width: $tablet) {
            margin-right: 0;
            margin-bottom: 0;
        }
        
    }
}

.contacts__subtitle {
    display: block;
    font-size: 18px;
    line-height: 25px;
    font-weight: 700;
    margin-bottom: 3px;

    @media (min-width: $tablet) {
        margin-bottom: 7px;
    }

    @media (min-width: $desktop) {
        margin-bottom: 24px;
    }
}

.contacts__link {
    cursor: pointer;
    font-weight: 300;
    font-size: 24px;
    line-height: 33px;
    color: $accent;
    

    &:hover,
    &:focus {
        color: $main-dark;
        
    }

    &:active {
        opacity: 0.3;
    }
}

.contacts__map-wrapper {
    position: relative;
    &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 32px;
        background-image: url(../../img/triangle.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: bottom center;

        @media (min-width: $tablet) {
            height: 68px;
        }

        @media (min-width: $desktop) {
            height: 123px;
        }
    }
}

.contacts__map {
    width: 100%;
    height: 200px;

    @media (min-width: $tablet) {
        height: 374px;
    }

    @media (min-width: $desktop) {
        height: 502px;
    }
}
