.review {
    text-align: center;
    padding-top: 50px;
    padding-bottom: 64px;

    @media (min-width: $tablet) {
        display: flex;
        flex-direction: column;
        padding-bottom: 41px;
    }

    @media (min-width: $desktop) {
        position: relative;
        padding-top: 193px;
        padding-left: 126px;
        padding-right: 126px;
        padding-bottom: 41px;

        &::before {
            position: absolute;
            content: '';
            background-image: url(../../img/quotes.svg);
            width: 79px;
            height: 63px;
            top: 72px;
            left: 50%;
            margin-left: -40px;
        }
    }
}

.review__name {
    display: block;

    font-style: normal;
    font-size: 18px;
    line-height: 30px;
    font-weight: 700;

    margin-bottom: 31px;
    
    @media (min-width: $tablet) {
        font-size: 20px;
        
        margin-bottom: 17px;
    }
}

.review__profession {
    display: none;
    margin-bottom: 31px;

    @media (min-width: $tablet) {
        display: block;
        margin-bottom: 0;
    }

    @media (min-width: $desktop) {

    }
}

.review__text {
    line-height: 30px;

    @media (min-width: $tablet) {
        order: -1;
        margin-bottom: 51px;

        font-size: 18px;
        line-height: 30px;
    }
}