.footer {
    padding-top: 40px;
    padding-bottom: 40px;

    @media (min-width: $tablet) {
        padding-top: 36px;
        padding-left: 20px;
        padding-right: 20px;
    }

    @media (min-width: $desktop) {
        padding-left: 0;
        padding-right: 0;
    }
}

.footer__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: $tablet) {
        flex-direction: row;
        justify-content: space-between;
    }

    @media (min-width: $desktop) {
        width: 950px;
        padding-left: 10px;
        padding-right: 10px;
        margin: 0 auto;
    }
}



.footer__logo-link {

    &:hover {
        .footer__logo--hover {
            fill: $accent;
        }
    }
}

.footer__logo-link--tablet {

    @media (min-width: $desktop) {
        display: none;
    }
}

.footer__logo-link--desktop {
    display: none;

    @media (min-width: $desktop) {
        display: block;
    }
}

.footer__logo {
    margin-bottom: 40px;

    @media (min-width: $tablet) {
        margin-bottom: 0;
    }
}



.footer__socials {
    margin-bottom: 40px;

    @media (min-width: $tablet) {
        margin-bottom: 0;
    }
}

.socials-list__item {
    cursor: pointer;
    padding-top: 5px;
    padding-left: 3px;

    &:hover,
    &:focus {
        border-color: $main-dark;
    }

    &:active {
        opacity: 0.1;
    }
}

.footer__copyright {
    display: flex;
    align-items: center;

    &:hover,
    &:focus {

        .footer__copyright-icon {
            fill: $main-dark;
        }
    }

    &:active {

        .footer__copyright-icon {
            opacity: 0.1;
        }
    }
}

.footer__directed {
    margin-right: 15px;
}