.header {
    position: absolute;
    z-index: 5;
    width: 100%;
    
    @media (min-width: $desktop) {
        background-color: $dark-transp;
        padding-top: 27px;
        padding-bottom: 42px;
    }  
}

.header__menu {

    @media (min-width: $desktop) {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        width: 940px;
        margin: 0 auto;
    }
}

.header__wrapper {
    display: flex;
    justify-content: space-between;

    padding-top: 21px;
    padding-bottom: 21px;
    padding-left: 20px;
    padding-right: 20px;

    background-color: $dark-transp;

    &--opened {
        background-color: $main-dark;
    }

    @media (min-width: $tablet) {
        padding-top: 27px;
        padding-bottom: 42px;
    }

    @media (min-width: $desktop) {
        margin: 0;
        padding: 0;
        background-color: transparent;
    }

}

.header__menu-button {
    @media (min-width: $desktop) {
        display: none;
    }
}

.header__menu-button:hover  {

    &:hover,
    &:focus {

        .header__icon {
            fill: $accent;
        }
    }

    &:active {

        .header__icon {
            opacity: 0.3;
        }
    }
}

.header__logo {

    &--mobile {
        @media (min-width: $tablet) {
            display: none;
        }
        @media (min-width: $desktop) {
            display: none;
        }
    }

    &--tablet {
        @media (min-width: $tablet) {
            display: block;
        }
        @media (min-width: $desktop) {
            display: none;
        }
    }
    
    &--desktop {
        @media (min-width: $desktop) {
            display: block;
        }
    }
}