.version-list__item {
    margin-bottom: 44px;
    
    &:last-child {
        margin-bottom: 35px;

        @media (min-width: $tablet) {
            margin-bottom: 47px;
        }
    }
}

.version-list__text {
    padding-left: 70px;
    font-size: 18px;
    line-height: 30px;
    padding-top: 5px;
    padding-bottom: 5px;
    position: relative;

    &:before {
        position: absolute;
        content: '';
        width: 37px;
        height: 37px;
        background-color: $light;
        left: 0;
        top: 50%;
        margin-top: -16.5px;
        border: 2px solid #E5E5E5;
        border-radius: 50%;
    }
    
    &::after {
        display: none;
        position: absolute;
        content: '';
        background-image: none;
        background-color: $accent;
        width: 16px;
        height: 16px;
        left: 10px;
        top: 50%;
        margin-top: -6px;
        border-radius: 50%;
    }

    &:hover,
    &:focus {

        &:before {
            border-color: $main-dark;
        }
    }

    &:active {

        &:before {
            border-color: $accent;
        }
    }
}

.version-list__radio:checked + .version-list__text {
    &::after {
        display: block;
    }
}

