

.slogan__text {
    font-size: 24px;
    font-weight: 300;
    line-height: 120%;
    text-align: center;

    &:first-child {
        margin-bottom: 25px;

        @media (min-width: $tablet) {
            margin-bottom: 0;
        }
    }

    &:last-child {

        @media (min-width: $tablet) {
            width: 380px;
            margin: 0 auto;
        }

        @media (min-width: $desktop) {
            width: 450px;;
        }
    }

    @media (min-width: $tablet) {
        font-size: 40px;
        line-height: 54px;
    }

    @media (min-width: $desktop) {
        font-size: 50px;
        line-height: 68px;
    }
}