.socials-list {
    width: 175px;
    display: flex;
    justify-content: space-between;
}

.socials-list__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    border: 3px solid rgba(40, 54, 69, 0.1);
    border-radius: 50%;
}