.main-form {
    padding-top: 54px;
}

.main-form__page-wrapper {
    
    @media (min-width: $desktop) {
        width: 960px;
        padding-left: 10px;
        padding-right: 10px;
        margin: 0 auto;
    }
}

.main-form__interlude {
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 55px;
}

.main-form__bg {
    background-color: $main-light;
    padding-top: 42px;

    @media (min-width: $tablet) {
        background-color: $light;
        padding-left: 22px;
        padding-right: 22px;  
    }

    @media (min-width: $desktop) {
        padding-left: 0;
        padding-right: 0;
    }
}

.main-form__radio-wrapper {
    margin-bottom: 37px;
}

.main-form__name-field {
    
    @media (min-width: $tablet) {
        border: 2px solid #E5E5E5;
        border-radius: 1px;  
        margin-bottom: 51px;
    }

    &--textarea {
        margin-bottom: 52px;
        

        @media (min-width: $tablet) {
            padding-bottom: 50px;
        }

        @media (max-width: $desktop) {
            margin-left: 22px;
            margin-right: 22px;
        }
    }
}

.main-form__container {
    padding-left: 20px;
    padding-right: 20px;

    @media (min-width: $tablet) {
        padding-left: 60px;
        padding-right: 60px;
    }

    
    
    &--name {

        @media (min-width: $tablet) {
            border: none;
            padding-left: 20px;
            padding-right: 20px;
        }

        @media (min-width: $desktop) {
            padding-left: 0;
            padding-right: 0;
        }
    }
}



.main-form__item {
    margin-bottom: 27px;

    @media (min-width: $tablet) {
        display: flex;
        align-items: center;
        margin-bottom: 56px;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.achievements-list__text {
    position: relative;
    font-size: 18px;
    line-height: 30px;
    padding-left: 59px;
    padding-top: 5px;
    padding-bottom: 5px;

    &:before {
        position: absolute;
        content: '';
        width: 37px;
        height: 37px;
        background-color: $light;
        left: 0;
        top: 50%;
        margin-top: -16.5px;
        border: 2px solid #E5E5E5;
        border-radius: 1px;
    }

    &::after {
        display: none;
        position: absolute;
        content: '';
        background-image: url(./../../img/check.svg);
        background-repeat: no-repeat;
        background-position: center;
        width: 37px;
        height: 37px;
        left: 0;
        top: 50%;
        margin-top: -16.5px;
    }

    &:hover,
    &:focus {

        &:before {
            border-color: $main-dark;
        }
    }

    &:active {

        &:before {
            border-color: $accent;
        }
    }
}

.main-form__checkbox:checked + .achievements-list__text {
    &::after {
        display: block;
    }
}

.main-form__input-title {
    display: block;
    color: $accent;
    text-transform: uppercase;
    margin-left: 23px;
    margin-bottom: 15px;
    cursor: pointer;

    @media (min-width: $tablet) {
        margin: 0;
        margin-right: 20px;
    }
}

.main-form__type-field {
    background-color: transparent;

    width: 100%;
    height: 63px;
    padding-left: 22px;
    font-size: 18px;
    line-height: 30px;
    font-weight: 700;

    border: 2px solid $border;
    border-radius: 1px;
    outline: none;

    &::placeholder {
        color: $main-dark;
    }

    &:hover {
        border-color: $main-dark;
    }

    
    &:focus,
    &:active {
        border-color: $accent;
    }
}

.main-form__fieldset-title {
    line-height: 18px;
    text-align: center;
    text-transform: uppercase;
    color: $accent;

    padding-left: 39px;
    padding-right: 39px;
    margin-bottom: 55px;

    &--mobile {
        display: none;
    }

    &--tablet {

        @media (min-width: $tablet) {
            display: block;
            margin-bottom: 30px;
        }
    }

    &--radio {
        margin-bottom: 40px;

        @media (min-width: $tablet) {
            margin-bottom: 51px;
        }
    }

    &--textarea {
        margin-bottom: 16px;
    }

    @media (min-width: $tablet) {
        line-height: 30px;
    }
}


.main-form__textarea-field {
    width: 100%;
    height: 199px;
    border: 2px solid $border;

    &::placeholder {
        font-size: 18px;
        line-height: 30px;
        color: $main-dark;
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 22px;
        padding-bottom: 22px;
    }
}

.main-form__submit-wrapper {
    padding-left: 22px;
    padding-right: 22px;
    padding-bottom: 28px;
    display: flex;

    @media (min-width: $tablet) {
        padding-bottom: 50px;
    }

    @media (min-width: $desktop) {
        padding-left: 0;
        padding-right: 0;
    }
}

.main-form__submit-inner {
    margin: 0 auto;

    @media (min-width: $tablet) {
        margin: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    
}

.main-form__button {
    background-color: $btn-form;
    border: none;

    font-size: 18px;
    line-height: 30px;
    font-weight: 700;
    text-transform: uppercase;
    color: $light;

    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 47px;
    padding-right: 47px;
    margin-bottom: 34px;

    border-radius: 1px;

    &:hover,
    &:focus {
        background-color: $hover-primary;
    }

    &:active {
        color: rgba(255, 255, 255, 0.3);
    }

    @media (min-width: $tablet) {
        padding-left: 55px;
        padding-right: 55px;

        margin-bottom: 0;
    }
}

.main-form__required {
    display: block;
    position: relative;
    text-transform: uppercase;
    padding-left: 15px;

    &::before {
        position: absolute;
        content: '*';
        left: 0;
        top: 3px;
        font-size: 38px;
        color: $accent;
    }
}