.modal {
    position: fixed;
    z-index: 10;
    top: 50px;
    left: 50%;
    margin-left: -160px;

    @media (min-width: $tablet) {
        margin-left: -230px;
    }

    @media (min-width: $desktop) {
        margin-left: -390px;
    }
}

.modal__wrapper {
    background-color: $light;

    width: 320px;
    text-align: center;

    box-shadow: 0px 7px 15px rgba(0, 1, 1, 0.15);
    border-radius: 1px;

    margin: 0 auto;

    @media (min-width: $tablet) {
        width: 460px;
    }

    @media (min-width: $desktop) {
        width: 780px;
    }
}

.modal__text-warpper {
    padding-left: 27px;
    padding-right: 27px;
    padding-top: 38px;
    padding-bottom: 34px;

    @media (min-width: $tablet) {
        padding-left: 82px;
        padding-right: 82px;
        padding-top: 49px;
    }

    @media (min-width: $desktop) {
        padding-left: 214px;
        padding-right: 214px;
        padding-top: 49px;
    }
}

.modal__title {
    display: block;

    font-weight: 700;
    font-size: 18px;
    line-height: 24px;

    margin-bottom: 24px;

    @media (min-width: $tablet) {
        font-size: 30px;
        line-height: 40px;
    }
}

.modal__text {

    @media (min-width: $tablet) {
        font-size: 18px;
    }
}

.modal__btn-wrapper {
    padding-left: 27px;
    padding-right: 27px;
    padding-bottom: 52px;

    &--ok {

        @media (min-width: $tablet) {
            padding-left: 83px;
            padding-right: 83px;
        }

        @media (min-width: $desktop) {
            background-color: $main-light;
            padding-top: 52px;
            padding-bottom: 52px;
            padding-left: 242px;
            padding-right: 242px;
        }
    }

    &--error {
        background-color: $main-light;
        padding-left: 27px;
        padding-right: 27px;
        padding-top: 52px;
        padding-bottom: 52px;

        @media (min-width: $tablet) {
            padding-left: 83px;
            padding-right: 83px;
        }
    
        @media (min-width: $desktop) {
            padding-left: 162px;
            padding-right: 162px;
        }
    }
}

.modal__btn {
    margin: 0;
    margin: 0 auto;

    width: 100%;

    @media (min-width: $tablet) {
    }

    &--error {

        @media (min-width: $tablet) {
        }
    
        @media (min-width: $desktop) {

        }
    }
}


.modal-show {
    display: block;
}

.modal-close {
    display: none;
}