@import '../mixins';
@import '../fonts';
@import '../vars';


html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    color: $main-dark;
    margin: 0;
    padding: 0;
}

a {
    display: block;
    text-decoration: none;
    cursor: pointer;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}



button {
    font: inherit;
    padding: 0;
    margin: 0;
    outline: none;
    background-color: transparent;
    border: none;
    cursor: pointer;
}


h1, h2, h3 {
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
    padding: 0;
}

img {
    display: block;
}

input {
    font: inherit;
    background: transparent;
    margin: 0;
    padding: 0;
}

fieldset {
    margin: 0;
    padding: 0;
    border: none;
}

blockquote {
    margin: 0;
}

tr,
td,
th {
    padding: 0;
}

.show {
    display: block;
}

.close {
    display: none;
}
