.main-button {
    display: inline-block;

    font-size: 18px;
    font-weight: 700;
    line-height: 18px;
    color: $light;

    border: 3px solid $light;
    border-radius: 29px;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 39px;
    padding-right: 39px;
    text-align: center;

    &:hover, 
    &:focus {
        background-color: $light;
        color: $accent;
        outline: none;
    }

    &:active {
        opacity: 0.3;
    }
}