.number-list {
    padding-top: 42px;
    padding-bottom: 44px;

    @media (min-width: $tablet) {
        padding-top: 0;
        padding-bottom: 22px;
    }

    @media (min-width: $desktop) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 70px;
    }
}

.number-list__item {
    
    &--tel {
        position: relative;

        
        &:before {
            position: absolute;
            content: '';
            width: 59px;
            height: 59px;
            background-color: $border;
            right: 2px;
            top: 2px;

            @media (max-width: $tablet) {
                display: none;
            }
        }

        &:after {
            position: absolute;
            content: '';
            width: 59px;
            height: 59px;
            right: 2px;
            top: 2px;
            background-image: url(./../../img/phone.svg);
            background-repeat: no-repeat;
            background-position: center;

            @media (max-width: $tablet) {
                display: none;
            }
        }
    }

    &--mail {
        position: relative;

        
        &:before {
            position: absolute;
            content: '';
            width: 59px;
            height: 59px;
            background-color: $border;
            right: 2px;
            top: 2px;

            @media (max-width: $tablet) {
                display: none;
            }
        }

        &:after {
            position: absolute;
            content: '';
            width: 59px;
            height: 59px;
            right: 2px;
            top: 2px;
            background-image: url(./../../img/mail.svg);
            background-repeat: no-repeat;
            background-position: center;

            @media (max-width: $tablet) {
                display: none;
            }
        }
    }

    @media (min-width: $tablet) {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        margin-bottom: 0;
    }

    &:first-child {

        @media (min-width: $tablet) {
            margin-bottom: 30px;
        }

        @media (min-width: $desktop) {
            margin-bottom: 0;
        }
    }
}

.number-list__input-title {

    @media (min-width: $tablet) {
        order: 2;
        margin-left: 19px;
    }

    
}

.number-list__type-field {

    @media (min-width: $tablet) {
        margin-bottom: 10px;
    }

    
}