@media (min-width: 660px) {
  .modal__title {
    font-size: 30px;
    line-height: 40px;
  }
}

@media (min-width: 660px) {
  .modal__text {
    font-size: 18px;
  }
}

.modal__btn-wrapper {
  padding-left: 27px;
  padding-right: 27px;
  padding-bottom: 52px;
}

@media (min-width: 660px) {
  .modal__btn-wrapper--ok {
    padding-left: 83px;
    padding-right: 83px;
  }
}

@media (min-width: 940px) {
  .modal__btn-wrapper--ok {
    background-color: #F2F2F2;
    padding-top: 52px;
    padding-bottom: 52px;
    padding-left: 242px;
    padding-right: 242px;
  }
}

.modal__btn-wrapper--error {
  background-color: #F2F2F2;
  padding-left: 27px;
  padding-right: 27px;
  padding-top: 52px;
  padding-bottom: 52px;
}

@media (min-width: 660px) {
  .modal__btn-wrapper--error {
    padding-left: 83px;
    padding-right: 83px;
  }
}

@media (min-width: 940px) {
  .modal__btn-wrapper--error {
    padding-left: 162px;
    padding-right: 162px;
  }
}

.modal__btn {
  margin: 0;
  margin: 0 auto;
  width: 100%;
}

.modal-show {
  display: block;
}

.modal-close {
  display: none;
}

@media (min-width: 940px) {
  .swiper-container {
    width: 820px;
  }
}

@media (max-width: 940px) {
  .swiper-button-prev {
    display: none;
  }
}

.swiper-button-prev::after {
  display: none;
}

@media (max-width: 940px) {
  .swiper-button-next {
    display: none;
  }
}

.swiper-button-next::after {
  display: none;
}
