.download {
    background-color: $download-bg;

    @media (min-width: $tablet) {
        background-color: transparent;
    }
}

.download__button {
    margin-bottom: 38px;

    @media (min-width: $tablet) {
        margin-bottom: 51px;
    }
}

.download__icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 204px;
    
    margin: 0 auto;
    margin-bottom: 31px;

    @media (min-width: $tablet) {
        margin-bottom: 42px;
        font-weight: 300;
    }
}



.download__text {
    font-weight: 300;
    color: $light;
    text-align: center;
}