.plans-list {
    width: 276px !important;
    padding-bottom: 55px;
}



.plans-list__head {
    padding-top: 19px;
    padding-bottom: 35px;

    text-align: center;
    color: $light;
    text-transform: uppercase;

    background-color: $main-dark;
    border: 2px solid $light;
}

.plans-list__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: -3px;
}

.plans-list__price {
    font-weight: 300;
    font-size: 18px;
    line-height: 30px;
}


.plans-list__options {
    background-color: $light;
}

.plans-list__option {
    display: flex;
    justify-content: space-between;

    border: 1px solid $main-light;
    padding-top: 25px;
    padding-bottom: 24px;
    padding-left: 20px;
    padding-right: 17px;
}

.plans-list__option-name {
    text-transform: uppercase;
}

