.features-list {
    background-color: $main-light;
    padding-top: 129px;

    @media (min-width: $tablet) {
        position: relative;
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 49px;
    }

    @media (min-width: $desktop) {
        position: static;
        display: flex;
        justify-content: space-between;
        background-color: transparent;
        width: 940px;
        margin: 0 auto; 
    }
}

.features-list__item {
    position: relative;
    text-align: center;
    width: 280px;
    margin: 0 auto;
    margin-bottom: 43px;

    @media (min-width: $tablet) {
        position: static;
        padding-left: 310px;
        text-align: left;
        width: auto;
        margin-bottom: 46px;
    }

    @media (min-width: $desktop) {
        position: relative;
        padding: 0;
        margin: 0;
        padding-top: 320px;
        text-align: center;
        width: 252px;
    }

    &::before {
        position: absolute;
        content: "";
        background-image: url(../../img/iphone-active.png);
        background-repeat: no-repeat;
        background-size: cover;
        width: 158px;
        height: 278px;
        bottom: 0;
        left: center;
        margin-left: -79px;

        @media (min-width: $tablet) {
            background-image: url(../../img/iphone-tablet.png);
            left: 20px;
            margin-left: 0;

            width: 268px;
            height: 481px;

            @media (min-resolution: 2dppx) {
                background-image: url(../../img/iphone-tablet@2x.png);
            }
        }

        @media (min-width: $desktop) {
            height: 265px;
            width: 186px;
            background-position: top center;
            background-size: 270px auto;
            top: 0;
            left: 50%;
            margin-left: -82px;
        }
        
    }

    &:first-child {
        &::before {
            display: none;

            @media (min-width: $desktop) {
                display: block;
                background-image: url(../../img/iphone-list-first.png);

                @media (min-resolution: 2dppx) {
                    background-image: url(../../img/iphone-list-first@2x.png);
                }
            }

            
        }

        
    }

    &:nth-child(2) {
        &::before {
            display: none;

            @media (min-width: $desktop) {
                display: block;
                background-image: url(../../img/iphone-list-second.png);

                @media (min-resolution: 2dppx) {
                    background-image: url(../../img/iphone-list-second@2x.png);
                }
            }
        }

        
    }

    &:last-child {
        
        @media (max-width: $tablet) {
            padding-bottom: 325px; 

        }

        &::before {
            

            @media (min-width: $desktop) {
                background-image: url(../../img/iphone-list-third.png);

                @media (min-resolution: 2dppx) {
                    background-image: url(../../img/iphone-list-third@2x.png);
                }
            }
        }
    }
}


.features-list__title {
    font-size: 18px;
    line-height: 24px;
    color: $dark;
    margin-bottom: 24px;

    @media (min-width: $desktop) {
        font-size: 30px;
        line-height: 30px; 
        margin-bottom: 35px;
    }
}

.features-list__text {

    @media (min-width: $desktop) {
        font-size: 18px;
        line-height: 30px;
    }
}