.intro__container {
    
    @media (min-width: $desktop) {
        width: 940px;
        margin: 0 auto;
    }
}

.intro__page-intro {
    padding-top: 95px;
    width: 100%;

    @media (min-width: $tablet) {
        position: relative;
        z-index: 1;
        padding-top: 212px;
        padding-bottom: 119px;
    } 

    @media (min-width: $desktop) {
        
    }
}

.intro__text-wrapper {
    width: auto;
    margin: 0 auto;

    @media (min-width: $tablet) {
        width: 230px;
        margin-left: 40px;
    }
}

.intro__intro-text {
    margin-bottom: 34px;
    
    @media (min-width: $tablet) {
        margin-bottom: 39px;
    }

    
}

.intro__img {
    bottom: 0;
    left: 0;

    @media (min-width: $tablet){
        position: absolute;
        z-index: -1;
    }

    @media (min-width: $desktop) {
        position: absolute;
        left: 20%;
    }
}

.intro__download-wrapper {
    width: 280px;
    margin: 0 auto;
    padding-top: 28px;
    padding-bottom: 38px;
    text-align: center;

    @media (min-width: $tablet) {
        margin-left: 20px;
        padding-bottom: 0;
    }
}




