.page-intro {
    background-image: url(../img/page-intro-bg-mobile.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: 2dppx) {
        background-image: url(../img/page-intro-bg-mobile@2x.jpg);
        background-position: top center;
    }

    @media (min-width: $tablet) {
        background-image: url(../img/page-intro-bg-tablet.jpg);
    }

    @media (min-width: $desktop) {
        background-image: url(../img/page-intro-bg-desktop.jpg);
    }
}

.page-intro__text {
    display: block;
    font-weight: 300;
    font-size: 24px;
    line-height: 120%;
    text-align: center;
    color: $light;

    @media (min-width: $tablet) {
        font-size: 40px;
    }

    @media (min-width: $desktop) {
        font-size: 50px;
        font-weight: 400;
    }
}


