.fact {
    display: none;
    margin-left: 20px;
    margin-right: 20px;

    @media (min-width: $tablet) {
        display: block;
        position: relative;
        padding-left: 300px;
        padding-top: 102px;
        padding-bottom: 50px;

        &::before {
            position: absolute;
            content: '';
            left: 0;
            top: 54px;
            width: 238px;
            height: 276px;
            background-image: url(../../img/fact-diagram.svg);
            background-repeat: no-repeat;
            background-size: contain;

            @media (min-width: $desktop) {
                width: 298px;
                height: 345px;
            }
        }
    }

    @media (min-width: $desktop) {
        width: 940px;
        margin: 0 auto;
        padding-left: 370px;
    }
}

.fact__title {
    font-size: 30px;
    line-height: 36px;
    font-weight: 700;
    color: $dark;

    @media (min-width: $tablet) {
        width: 168px;
        margin-bottom: 24px;
    }

    @media (min-width: $desktop) {

        width: auto;
        margin-bottom: 37px;
    }
}

.fact__text {
    font-size: 14px;
    line-height: 24px;

    @media (min-width: $tablet) {
        margin-bottom: 24px;
    }

    @media (min-width: $desktop) {
        font-size: 18px;
        line-height: 30px;
        margin-bottom: 47px;
    }
}

.fact__link {
    color: $accent;
    text-decoration: underline;

    &:hover,
    &:focus {
        color: $main-dark;
    }

    &:active {
        color: $main-dark;
        opacity: 0.3;
    }

    @media (min-width: $desktop) {
        font-size: 18px;
        line-height: 30px;
    }
}